import axios from 'axios';
import * as _ from 'lodash';

const resolveUrl = (entity) => {
  return `https://db.ygoprodeck.com/api/v7/${entity}.php`;
};

const cardsUrl = resolveUrl('cardinfo');

const archetypesUrl = resolveUrl('archetypes');

const cardSetsUrl = resolveUrl('cardsets');

export const searchCards = (params, pageInfo) => {
  return axios.get(cardsUrl, fixParams(params, pageInfo));
};

export const fetchArchetypes = () => {
  return axios.get(archetypesUrl);
};

export const fetchCardSets = () => {
  return axios.get(cardSetsUrl);
};

const fixParams = (params, pageInfo) => {
  const processedPageInfo = {
    num: (pageInfo.size).toString(),
    offset: ((pageInfo.page - 1) * pageInfo.size).toString()
  };

  const combinedParams = {
    ...params,
    ...processedPageInfo
  };

  const cleanParams = { params: _.pickBy(combinedParams) };

  return cleanParams;
};