import React, { useState, useEffect } from 'react';
import { searchCards } from '../../service/ygodatabaseservice';
import Loading from '../../components/Loading';
import CardSearchContext, { CardSearchProvider } from './context/CardSearchContext';
import Card from 'react-bootstrap/Card';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import CardGallery from '../../components/CardGallery';
import CardList from '../../components/CardList';
import CardGrid from '../../components/CardGrid';
import ResultInfo from './components/ResultInfo';
import CardView from './components/CardView';
import CardSearchSettings from './components/CardSearchSettings';
import CardSearchPagination from './components/CardSearchPagination';
import CardSearchForm from './components/CardSearchForm';
import NoResultsFound from './components/NoResultsFound';

const initialParams = {
  name: '',
  fname: '', 
  desc: '', 
  effect: '', 
  sort: 'Name', 
  sortorder: 'asc', 
  num: '', 
  offset: '', 
  type: '', 
  atk: '', 
  def: '', 
  level: '', 
  race: '', 
  attribute: '', 
  set: '', 
  rarity: '', 
  archetype: '', 
  set: '', 
  banlist: '', 
  link: '', 
  scale: '', 
  linkmarker: '', 
  format: '', 
  staple: '', 
  misc: '', 
  includeAliased: '', 
  startdate: '', 
  enddate: '', 
  dateregion: '', 
  startprice: '', 
  endprice: '', 
  num: '', 
  offset: ''
};

const initialPageInfo = {
  size: 10,
  page: 1
};

const CardSearch = () => {
  const [searchResults, setSearchResults] = useState(null);
  const [loadingResults, setLoadingResults] = useState(false);
  const [params, setParams] = useState(initialParams);
  const [pageInfo, setPageInfo] = useState(initialPageInfo);
  const [viewMode, setViewMode] = useState('Gallery');

  useEffect(() => {
    search(params, pageInfo);
  }, []);

  const search = (params, pageInfo) => {
    setLoadingResults(true);
    searchCards(params, pageInfo)
      .then(res => setSearchResults(res.data))
      .catch(err => setSearchResults(null))
      .finally(() => setLoadingResults(false));
  };

  const reset = () => {
    setParams(initialParams);
    setPageInfo(initialPageInfo);
    search(initialParams, initialPageInfo);
  }

  return (
    <>
      <CardSearchProvider value={{ params, setParams, pageInfo, setPageInfo, search, reset, searchResults, viewMode, setViewMode }}>
        <CardSearchForm />

        <div className="mt-3"></div>

        {searchResults
          ? <Card>
              <Card.Header>
                <Row>
                  <Col sm={12} md={4}>
                    <ResultInfo />
                  </Col>
                  <Col sm={12} md={8}>
                    <CardSearchSettings />
                  </Col>
                </Row>
              </Card.Header>
              <Card.Footer className="d-flex justify-content-center">
                <CardSearchPagination />
              </Card.Footer> 
              <Card.Body className="p-0">
                {loadingResults 
                  ? <Loading /> 
                  : <CardView viewMode={viewMode} cards={searchResults.data} />
                }
              </Card.Body> 
              <Card.Footer className="d-flex justify-content-center">
                <CardSearchPagination />
              </Card.Footer> 
            </Card>
          : <Card body>
              <NoResultsFound />
            </Card> 
        }
      </CardSearchProvider>
    </>
  );
};

export default CardSearch;
